// import { stringify } from "query-string";
import axios from 'axios';

import {
  GET_LIST,
  GET_ONE,
  GET_MANY,
  // UPDATE_MANY,
  // DELETE_MANY,
  // GET_MANY_REFERENCE,
  CREATE,
  UPDATE,
  DELETE,
} from "react-admin";

const convertFileToBase64 = file => new Promise((resolve, reject) => {
  const reader = new FileReader();
  if (file.rawFile instanceof Blob){
    reader.readAsDataURL(file.rawFile);
    reader.onload = () => resolve(reader.result);
    reader.onerror = reject;
  } else 
    resolve(file);
});


export default (apiUrl) => (type, resource, params) =>  { 
    switch (type) {
      case GET_LIST: {
        const { page, perPage } = params.pagination
        const url = `${apiUrl}/${resource}?page=${page}&pageSize=${perPage}`
        return axios.get(url).then(res => {return {data: res.data, total: res.data.length} })
      }
      case GET_ONE: {
          const url = `${apiUrl}/${resource}/${params.id}`
          return axios.get(url).then(res => {return {data: res.data}})
      }
      case UPDATE:
      case CREATE: {
          const url = `${apiUrl}/${resource}`
          if ((resource === "users" || resource === "adverts") && params.data.photo){
            return convertFileToBase64(params.data.photo).then(
              photo => ({ src: photo, title: 'photo' })
            ).then(photoBase64 => (
              axios.post(url, {...params.data, photo: photoBase64.src })
            ).then(res => {return {data: res.data}}))
          } else {
            const payload = params.data
            return axios.post(url, payload).then(res => {return {data: res.data}})
          }
      }
      // case CREATE: {
      //     const url = `${apiUrl}/${resource}`
      //     if ((resource === "users" || resource === "adverts") && params.data.photo){
      //       return convertFileToBase64(params.data.photo).then(
      //         photo => ({ src: photo, title: 'photo' })
      //       ).then(photoBase64 => (
      //         axios.post(url, {...params.data, photo: photoBase64.src })
      //       ).then(res => {return {data: res.data}}))
      //     } else {
      //       const payload = params.data
      //       return axios.post(url, payload).then(res => {return {data: res.data}})
      //     }
      // }
      case DELETE: {
          const url = `${apiUrl}/${resource}/${params.id}`
          return axios.delete(url).then(res => {return {data: res.data}})
      }
      case GET_MANY: {
        const ids = "ids=" + params.ids.join(',')
        const url = `${apiUrl}/${resource}?${ids}`;
        return axios.get(url).then(res => {return {data: res.data}})
      }
      default: {
        throw new Error(`–ù–µ–ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ–º—ã–π —Ç–∏–ø –∑–∞—Ä–æ—Å–∞ -  ${type}`)
      }
    //   case GET_MANY_REFERENCE: {
    //       alert()
    //     const { page, perPage } = params.pagination;
    //     url = `${apiUrl}/${resource}?page=${page}&pageSize=${perPage}`;
    //     break;
    //   }
  }
}

